* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  color: #212121;
  line-height: 1.5;
  background-size: 21.6px 21.6px;
  background-repeat: repeat;
  background-position: 10px 1px, 20.4px 11.4px;
  background-color: #fafafa;
  background-image: radial-gradient(circle, #514b4b8e 1px, transparent 1px);
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.country-list {
  font-size: 25px;
  font-weight: 600;
}
.country-flag {
  margin-right: 15px;
}
.country-item-name {
  font-size: 35px;
  font-weight: 500;
}

.country-description-title {
  font-size: 20px;
  font-weight: 500;
}
.country-description-data {
  vertical-align: center;
  font-weight: 400;
}
